html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-weight: 400;
  font-size: 16px;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  user-select: none !important;
  pointer-events: none !important;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
  align-items: center;
}

.title {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: clamp(1.875rem, 1.397rem + 1.96vw, 2.5rem);
  line-height: 135%;
  text-transform: capitalize;
  color: var(--black-color);
}

.text {
  margin: 0;
  font-family: var(--font-family);
  line-height: 24px;
  color: var(--text-color);
  font-weight: 400;
  font-size: 16px;
}

.color-text {
  margin: 0;
  color: var(--accent-color);
}

@include small-tablet {
  .title {
    line-height: 130%;
  }
  .text {
    font-size: 12px;
    line-height: 120%;
  }
}
@include mobile {
  .text {
    line-height: 110%;
  }
}
